import { checkLogin } from '@/common/utils.js'
import api from '@/common/api.js'
export default{
	components:{
		
	},
	data(){
		return {
			
		}
	},
	computed: {
		
	},
	onLoad(){
		
	},
	methods:{
		//退出登录
		loginOut(){
			checkLogin().then(() => {
				this.$refs.uniPopupOut.open()
			})
		},
		outBtn(){
			this.$http({
				api: api.updateToken,
				method: 'POST',
				params:{
					_method:'DELETE'
				}
			}).then(res => {
				uni.clearStorageSync()
				this.$store.commit('clearUserInfo')
				uni.redirectTo({
					url:'/pages/login/index'
				})
			}).catch(err => {
															
			})
		},
		//注销账户
		loginOff(){
			checkLogin().then(() => {
				this.$refs.uniPopupOff.open()
			})
		},
		offBtn(){
			this.$http({
					api: api.getUserInfo,
					method: 'POST',
					params:{
						_method:'DELETE'
					}
			}).then(res => {
					uni.showToast({
						title:'注销成功',
						success:() => {
							uni.clearStorageSync()
							this.$store.commit('clearUserInfo')
							uni.reLaunch({
								url:'/pages/login/index'
							})
						}
					})
			}).catch(err => {
															
			})
		}
	}
}
